<template>
  <section class="deposits">
    <header class="deposits__header">
      <Heading>{{ t('deposits.title') }}</Heading>
    </header>
    <main class="deposits__container">
      <Tabs
        v-model="activeTab"
        class="deposits__container__tabs"
        :data="tabs"
        :inactive-opacity="0.5"
        @update:model-value="changeTab"
      >
        <template #default="{ item }">
          {{ item.title }}
        </template>
      </Tabs>
      <keep-alive>
        <component
          :is="activeTab.component"
        />
      </keep-alive>
    </main>
  </section>
</template>

<script>
import { computed, defineAsyncComponent, onBeforeUnmount, ref } from 'vue'
import { useStore } from 'vuex'
import { useI18n } from 'vue-i18n'
import Heading from '@/components/Admin/Heading'
import Tabs from '@/components/UIKit/Tabs'
import depositsModule from '@/store/modules/admin/deposits'
import { useRoute, useRouter } from 'vue-router'
import { MODULES_NAMES } from '@/common/modules'

export default {
  name: 'Deposits',
  components: {
    Tabs,
    Heading,
    DepositTab: defineAsyncComponent(() => import('@/views-admin/treasury/Deposits/Tabs/DepositTab')),
    DepositHistoryTab: defineAsyncComponent(() => import('@/views-admin/treasury/Deposits/Tabs/DepositHistoryTab'))
  },
  setup () {
    const router = useRouter()
    const route = useRoute()
    const store = useStore()
    const { t } = useI18n()
    const tabs = computed(() => [
      { title: t('deposits.title'), key: 'new', icon: 'plus', component: 'DepositTab' },
      { title: t('common.history'), key: 'history', icon: 'stack', component: 'DepositHistoryTab' }
    ])
    const activeTab = ref(tabs.value.find(el => el.key === route.query.t) || tabs.value[0])

    if (!store.hasModule(MODULES_NAMES.DEPOSITS)) {
      store.registerModule(MODULES_NAMES.DEPOSITS, depositsModule)
    }
    onBeforeUnmount(() => {
      if (store.hasModule(MODULES_NAMES.DEPOSITS)) {
        store.unregisterModule(MODULES_NAMES.DEPOSITS)
      }
    })
    const changeTab = (tab) => {
      activeTab.value = tab
      router.replace({ ...route, query: { t: tab.key } })
    }

    return {
      t,
      tabs,
      activeTab,
      changeTab
    }
  }
}
</script>

<style lang="scss">
.deposits {
  height: 100%;
  padding: 30px 40px;
  display: flex;
  flex-direction: column;

  &__header {
    margin-bottom: 40px;
  }
  &__container {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
  }
}
</style>
